import {Link} from 'react-router-dom';
import React from 'react';
import favicon from './logo32.png'
import ThemeSelectorButton from './ThemeSelectorButton';

function Header(props){
    
    return(
    <header className="sticky top-0 text-gray-600 dark:text-gray-400 bg-gray-200 dark:bg-black body-font w-100">
        <div id="nav" className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
    
            <Link to='/' className="flex title-font font-medium items-center text-gray-900 dark:text-white mb-4 md:mb-0">
            <img src={favicon} width="32" height="32" alt="PTracker" />
            <span className="ml-3 text-xl">PTracker</span>
            </Link>
    
            <nav className="md:mr-auto md:ml-4 md:py-1 md:pl-4 md:border-l md:border-gray-400 dark:md:border-gray-700 flex flex-wrap items-center text-base justify-center">
            {(props.routes ?? []).map((component, index) => (
                (component.display ?? true) ?
                <React.Fragment key={index}>
                    <Link to={component.path} className="mr-5 hover:text-gray-900 dark:hover:text-white">{ component.text }</Link>
                </React.Fragment>
                : null
                )
            )}
            </nav>
        
            <div className="inline-flex items-center border-0 py-1 px-3 text-base gap-3 mt-4 md:mt-0">
            <ThemeSelectorButton/>
            </div>
        </div>
    </header>
    )
}

export default Header;