import {
    HashRouter as Router,
    Switch,
    Route,
    Redirect
} from 'react-router-dom';
import React, { useEffect } from 'react';
import Home from './Home';
import FourOFour from './Display404';
import Archive from './Archive';
import Post from './Post';
import About from './About';

function AppRoute(){
    
    useEffect(()=>{
    }, [])

    return(
    <Router>
        <Switch>
            <Route
                exact
                path="/"
                component={(props) => (
                <React.Fragment>
                    <Home {...props}/> 
                </React.Fragment>
                )}
            />
            <Route
                exact
                path="/archive/:page?"
                component={(props) => (
                <React.Fragment>
                    <Archive {...props}/> 
                </React.Fragment>
                )}
            />
            <Route
                exact
                path="/about"
                component={(props) => (
                <React.Fragment>
                    <About {...props}/> 
                </React.Fragment>
                )}
            />
            <Route
                exact
                path="/post/:id"
                component={(props) => (
                <React.Fragment>
                    <Post {...props}/> 
                </React.Fragment>
                )}
            />
            <Route
                exact
                path="/404"
                component={() => (
                <React.Fragment>
                    <FourOFour/>
                </React.Fragment>
                )}
            />
            <Redirect from='*' to='/404' />
        </Switch>
    </Router>
)}

export default AppRoute;