import React, {useEffect, useState} from 'react';

function ThemeSelectorButton(){
    let icons = {"light": <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
        </svg>, "dark": <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
        </svg>};
    
    const [theme, setTheme] = useState(localStorage.theme ?? (window.matchMedia('(prefers-color-scheme: dark)').matches ? "dark" : "light"))

    useEffect(()=>{    
        updateTheme(theme)
    }, [theme]);

    function toggleTheme(){
        if (theme === 'dark'){
            localStorage.theme = 'light'
            setTheme('light')
        }else{
            localStorage.theme = 'dark'
            setTheme('dark')
        }
    }

    function updateTheme(theme){
        if (theme === 'dark') {
            document.getElementsByTagName('html')[0].classList.add('dark')
        } else {
            document.getElementsByTagName('html')[0].classList.remove('dark')
        }
    }

    return(
        <button onClick={toggleTheme} className="focus:outline-none rounded bg-gray-300 hover:bg-gray-400 dark:bg-gray-800 dark:hover:bg-gray-700">
            {icons[theme ?? 'light']}
        </button>
    )
}

export default ThemeSelectorButton;