import React from "react";

function FBIframe(props){
    return (<iframe title={props.title} src={props.src} width="500" height="498" className="border-none overflow-hidden mx-auto" scrolling="no" frameBorder="0" allowFullScreen={true} allow="autoplay; clipboard-write; encrypted-media; picture-in-picture; web-share"></iframe>)
}

export default FBIframe