import React, { useEffect, useState } from 'react';
import { Redirect, useHistory } from 'react-router-dom';
import BaseTemplate from '../components/BaseTemplate';
import sa from '../services/superagent';
import ReactMarkdown from 'react-markdown';
import gfm from 'remark-gfm';
import remarkMath from 'remark-math'
import rehypeKatex from 'rehype-katex'
import 'katex/dist/katex.min.css'
import INIT_URL from '../atoms/setting';

function Post(props){
    const [post, setPost] = useState(null)
    let history = useHistory();
    const URL_ENDPOINT = "/data/posts/"
    const URL_SUFFIX = ".md"

    useEffect(()=>{
        let postID = props.match.params.id ?? ''
        if (postID !== ''){
            sa.get((localStorage.host_url ?? INIT_URL)+URL_ENDPOINT + postID.toString() + URL_SUFFIX).then((response)=>{
                if (response.ok){
                    setPost(response.text)
                }else{
                    setPost(false)
                }
            })
        }else{
            setPost(false)
        }
    }, [props.match.params.id]); 

    function children(post){
        if (post === false){
            return <Redirect to="/archive/" />
        }else{
            if (post !== null ){
                return (
                <>
                <div className="p-3 block"><ReactMarkdown remarkPlugins={[gfm, remarkMath]} rehypePlugins={[rehypeKatex]} 
                children={post} components={{
                    h1: ({node, children, ...props}) => <h1 className="text-center text-2xl font-bold" {...props} >{children}</h1>,
                    h2: ({node, children, ...props}) => <h2 className="text-center text-l font-bold" {...props} >{children}</h2>,
                    p: ({node, ...props}) => <p className="px-10"{...props} />
                }}/>
                </div>
                <button onClick={()=>history.goBack()} className="absolute bottom-10 right-10 h-10 w-20 py-1 mx-1 bg-white rounded-md dark:bg-gray-600 text-gray-700 dark:text-gray-400 transition-colors duration-200 transform hover:bg-indigo-600 dark:hover:bg-indigo-500 hover:text-white dark:hover:text-gray-100">Back</button>
                </>
                )
            }
        }
        return <></>
    }

    return(
        <>
        <BaseTemplate children={children(post)}/>
        </>
    )
}

export default Post;