import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import BaseTemplate from '../components/BaseTemplate';
import FBIframe from '../components/fb_iframe';
import sa from '../services/superagent';
import INIT_URL from '../atoms/setting';

function Home(){
    const [state, setState] = useState({})
    let history = useHistory()
    useEffect(()=>{
        let tempState = {}
        sa.get( (localStorage.host_url ?? INIT_URL)+"/data/archives/archive_0.json").then((response)=>{
            if (response.ok){
                tempState = response.body
                sa.get((localStorage.host_url ?? INIT_URL)+"/data/feed/lfeed.json").then((response)=>{
                    if (response.ok){
                        tempState['feeds'] = response.body
                    }
                    setState(tempState)
                })
            }else{
                setState(false)
            }
        })
        document.getElementsByTagName('html')[0].classList.add("overflow-x-hidden")
        return () => {
            document.getElementsByTagName('html')[0].classList.remove("overflow-x-hidden")
        }
    }, []);

    function content(data){
        let base = 
        <div className="p-3 block">
            <h1 className="text-center text-2xl font-bold">Latest Post</h1>
        </div>

        let feedsFrames = []
        
        if (data === false){
            return base
        }else if (Object.keys(state).length < 1){
            return base
        }else{
            if ((data.feeds ?? []).length > 0 ){
                data.feeds.map((feed, ind)=>(
                    feed.type === "facebook" ?
                    feedsFrames.push(<FBIframe key={"feed_"+ind.toString()} title="latest_facebook" src={feed.src}/>) :
                    feedsFrames.push(<span className="border-none overflow-hidden mx-auto" key={"feed_"+ind.toString()} dangerouslySetInnerHTML={{__html: feed.src}}/>)
                ))
            }

            return (
            <div className="dark:text-gray-400 dark:bg-gray-900 text-gray-600 body-font p-3 block h-full">
                <h1 className="text-center text-2xl font-bold">Latest Feed</h1>
                <div className="no-scrollbar overflow-x-auto w-full gap-5 grid grid-flow-row grid-col-1 p-5">
                {
                    feedsFrames
                }
                </div>
                <h1 className="text-center text-2xl font-bold">Latest Post</h1>
                <div className="no-scrollbar overflow-x-auto w-full gap-5 grid grid-flow-row grid-col-1 p-5">
                {data.data !== null && data.data.length > 0 ? 
                    <div key="entry_0" onClick={()=>history.push('/post/'+data.data[0].link)} className="w-full max-w-sm px-4 py-3 mx-auto bg-white rounded-md shadow-md cursor-pointer dark:bg-gray-800">
                        <div className="flex items-center justify-between">
                            <span className="text-sm font-light text-gray-800 dark:text-gray-400">{data.data[0].lastUpdated}</span>
                            <span className="px-3 py-1 text-xs text-indigo-800 uppercase bg-indigo-200 rounded-full dark:bg-indigo-300 dark:text-indigo-900">{data.data[0].author}</span>
                        </div>
                        <div>
                            <h1 className="mt-2 text-lg font-semibold text-gray-800 dark:text-white">{data.data[0].title}</h1>
                            <p className="mt-2 text-sm text-gray-600 dark:text-gray-300">{data.data[0].summary}</p>
                        </div>
                    </div>
                    :
                    <></>
                }
                </div>
            </div>
            )
        }
    }

    return(
        <>
        <BaseTemplate children={content(state)}/>
        </>
    )
}

export default Home;